.Thumb {
    width: 100%;
    height: 255px;
    margin-bottom: 20px;
    position: relative;
    cursor: pointer;
    border-radius: 10px;

    @media screen and (min-width: 1020px) {
        width: 30%;
        height: 340px;
        gap: 60px;
    }

    img {
        height: 100%;
        width: 100%;
        object-fit: cover;
        border-radius: 10px;
    }

    h2 {
        position: absolute;
        color: #fff;
        font-size: 18px;
        font-weight: 700;
        bottom: 10px;
        left: 20px;
        text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    }
}